*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.title{
    text-align: center;
    margin: 10px auto;
    color: #de6e58;
    padding: 10px;
}

.container{
    width: 100vw;
    height: calc(100vh - 79.2px);
    border: 1px solid #eee;
    background-color: #fffc08;
    padding: 10px;
    overflow: auto;
    .row-container{
        display: flex;
        flex-direction: column;
        .row,.rows{
            display: flex;
            flex-direction: row;
            position: relative;
            margin-left: 95px;
            width: fit-content;
            &:first-child{
                .type-row{
                    border-radius: 5px;
                }
            }
            .type-row{
                width: 75px;
                height: 30px;
                color: #fff;
                font-size: 20px;
                font-weight: bold;
                display: flex;
                justify-content: center;
                align-items: center;
                border: 1px solid #827c00;
            }
            .type-row.status{
                border: 1px solid #c886f0;
                background-color: #fff;
                color: #000;
            }
        }
    }
    .column{
        display: flex;
        flex-direction: column;
        position: relative;
        margin-top: -540px;
        margin-left: 10px;
        width: fit-content;
        .type-column{
            width: 75px;
            height: 30px;
            color: #fff;
            font-size: 20px;
            font-weight: bold;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 5px;
            border: 1px solid #827c00;
        }
    }
}